body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.content{
  min-height: calc(100vh - 70px - 64px);
}

.preloader-wrapper{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 70px auto;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
}

.card-content{
  flex-grow: 1;
}

img,iframe{
  max-width: 100%;
}

iframe{ 
  width: 600px;
  height: 350px;
}

.textnames{
  display: flex;
  flex-wrap: wrap;
    flex-direction: column;
    align-content: center;
    align-items: center;
}

.spanbold{
  font-weight: bold;
}

.pupupu{
  margin-top: 70px;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  flex-direction: row;
}
.aboutblock{
  display: flex;
  margin: 50px 0px;
}
.textbl{
  width: 50%;
}
.imgbl{
  width: 50%;
}
